import React, { useCallback, useReducer } from 'react'
import { createReducer } from '@reduxjs/toolkit'

import { CalibrationSettings } from './controls/CalibrationSettings'
import { Indicators } from './controls/Indicators'
import { Name } from './controls/Name'
import { OremSettings } from './controls/OremSettings'
import { RreSettings } from './controls/RreSettings'
import { VoltageLevels } from './controls/VoltageLevels'
import styles from './SideBar.module.less'

const BLOCKS = {
  calibration: 'calibration',
  name: 'name',
  rreSettings: 'rreSettings',
  voltageLevels: 'voltageLevels',
  oremSettings: 'oremSettings',
  indicators: 'indicators',
}
const initialActiveSettings = {}
const settingsReducer = createReducer(initialActiveSettings, {
  toggle: (state, action) => {
    const prevState = state[action.payload]
    state = {}
    // clear
    state[action.payload] = !prevState
    return state
  },
})

export const SideBar = () => {
  const [activeSettings, setActiveSettings] = useReducer(settingsReducer, initialActiveSettings)
  const activateSettings = blockKey => {
    return useCallback(() => {
      return setActiveSettings({ type: 'toggle', payload: blockKey })
    }, [blockKey])
  }

  return (
    <div className={styles.container}>
      <Name isActive={activeSettings[BLOCKS.name]} setActiveSettings={activateSettings(BLOCKS.name)} />

      <RreSettings
        isActive={activeSettings[BLOCKS.rreSettings]}
        setActiveSettings={activateSettings(BLOCKS.rreSettings)}
      />

      <VoltageLevels
        isActive={activeSettings[BLOCKS.voltageLevels]}
        setActiveSettings={activateSettings(BLOCKS.voltageLevels)}
      />

      <OremSettings
        isActive={activeSettings[BLOCKS.oremSettings]}
        setActiveSettings={activateSettings(BLOCKS.oremSettings)}
      />

      <CalibrationSettings
        isActive={activeSettings[BLOCKS.calibration]}
        setActiveSettings={activateSettings(BLOCKS.calibration)}
      />

      <Indicators
        isActive={activeSettings[BLOCKS.indicators]}
        setActiveSettings={activateSettings(BLOCKS.indicators)}
      />
    </div>
  )
}
