import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem } from 'rc-form-pure'

import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { TextField } from '../../../../components/ui/form/fields/TextField'
import { SwitchField } from '../../../../components/ui/form/fields/SwitchField'
import { Button } from '../../../../components/ui/Button'

import { Container } from './Container'
import styles from './controls.module.less'

export const CalibrationSettings = ({ isActive, setActiveSettings }) => {
  const handleSubmit = () => {
    // api request
    setActiveSettings()
  }

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Настройка калибровки <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem
            name="MonthsAmount"
            label="Количество мес."
            type="number"
            underline
            labelSize="small"
            component={TextField}
          />
          <FormItem name="UseWeekDayMapping" label="Учитывать дни недели" labelSize="small" component={SwitchField} />
          <FormItem name="UseDayTypeMapping" label="Учитывать типы дней" labelSize="small" component={SwitchField} />

          <div className={styles.submitContainer}>
            <Button block type="primary" size="large" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>Тип</label>
          <span className={styles.value}>Абсолютный отбор</span>

          <label className={styles.label}>Какое то длинное название</label>
          <span className={styles.value}>3</span>
        </div>
      )}
    </Container>
  )
}

CalibrationSettings.propTypes = {
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
}
