import React from 'react'
import PropTypes from 'prop-types'
import cn from 'classnames'

import styles from './Label.module.less'

const SIZES = ['small', 'medium', 'large']

/**
 * @param {object} props
 * @param {'small' | 'medium' | 'large'} props.size
 */
export const Label = ({ required, children, inline, className, size, ...props }) => {
  return (
    <label
      {...props}
      className={cn(styles.label, className, { _required: required, [`_${size}`]: size, _inline: inline })}
    >
      <span>{children}</span>
    </label>
  )
}

export const LabelSizeType = PropTypes.oneOf(SIZES)

Label.propTypes = {
  size: LabelSizeType,
  className: PropTypes.string,
  inline: PropTypes.bool,
  required: PropTypes.bool,
  children: PropTypes.node,
}

Label.defaultProps = {
  inline: false,
}
