import React from 'react'
import PropTypes from 'prop-types'
import { Select as SelectAnt } from 'antd'

import { Label, LabelSizeType } from '../Label'
import { BaseField } from '../BaseField'

import styles from './SelectField.module.less'

const { Option } = SelectAnt
const filterOption = (inputValue, option) => option.children.toLowerCase().match(inputValue.toLowerCase())

export const SelectField = ({ style, label, paddingBottom, options, container, required, labelSize, ...props }) => {
  return (
    <BaseField style={style} {...props}>
      {parsedProps => (
        <>
          <Label required={required} size={labelSize}>
            {label}
          </Label>
          <SelectAnt
            height="auto"
            notFoundContent="Not found"
            {...parsedProps}
            className={styles.select}
            // filters on backend when onSearch passed
            filterOption={props.onSearch ? x => x : filterOption}
          >
            {options?.map(({ label, value }) => (
              <Option key={value} value={value}>
                {label}
              </Option>
            ))}
          </SelectAnt>
        </>
      )}
    </BaseField>
  )
}

SelectField.propTypes = {
  labelSize: LabelSizeType,
  style: PropTypes.object,
  container: PropTypes.bool,
  direction: PropTypes.string,
  paddingBottom: PropTypes.bool,
  required: PropTypes.bool,
  label: PropTypes.string,
  error: PropTypes.string,
  options: PropTypes.arrayOf(
    PropTypes.shape({
      label: PropTypes.string,
      value: PropTypes.string,
    })
  ),
  onSearch: PropTypes.func,
}
