import React from 'react'
import PropTypes from 'prop-types'
import { FormBuilder, FormItem } from 'rc-form-pure'

import { ReactComponent as EditIcon } from '../../../../components/ui/icons/edit.svg'
import { TextField } from '../../../../components/ui/form/fields/TextField'
import { SelectField } from '../../../../components/ui/form/fields/SelectField'
import { Button } from '../../../../components/ui/Button/Button'

import { Container } from './Container'
import styles from './controls.module.less'

const priceCategoryItems = [
  { value: 'THIRD', label: 'третья' },
  { value: 'FOURTH', label: 'четвёртая' },
  { value: 'FIFTH', label: 'пятая' },
  { value: 'SIXTH', label: 'шестая' },
]

export const RreSettings = ({ region, isActive, setActiveSettings }) => {
  const handleSubmit = () => {
    // api request
    setActiveSettings()
  }

  return (
    <Container isActive={isActive}>
      <span className={styles.title} role="button" onClick={setActiveSettings}>
        Настройки РРЭ <EditIcon className={styles.titleIcon} />
      </span>

      {isActive ? (
        <FormBuilder withForm onSubmit={handleSubmit}>
          <FormItem name="SupplierId" label="ID поставщика" underline labelSize="small" component={TextField} />
          <FormItem
            name="PriceCategory"
            label="Ценовая категория"
            labelSize="small"
            options={priceCategoryItems}
            component={SelectField}
          />

          <div className={styles.submitContainer}>
            <Button block type="primary" size="large" className="asd">
              Сохранить
            </Button>
          </div>
        </FormBuilder>
      ) : (
        <div className={styles.values}>
          <label className={styles.label}>Регион</label>
          <span className={styles.value}>{region}</span>

          <label className={styles.label}>Гарантирующий поставщик</label>
          <span className={styles.value}>ПЭСТ</span>

          <label className={styles.label}>Мощность устройств</label>
          <span className={styles.value}>Мощность устройств</span>

          <label className={styles.label}>Текущая ЦК</label>
          <span className={styles.value}>3 ЦК</span>
        </div>
      )}
    </Container>
  )
}

const mockProps = {
  region: 'Татарстан',
}

RreSettings.propTypes = {
  region: PropTypes.string,
  isActive: PropTypes.bool,
  setActiveSettings: PropTypes.func.isRequired,
}
RreSettings.defaultProps = mockProps
