import React from 'react'
import PropTypes from 'prop-types'
import cn from 'classnames'
import { Input as AntInput, InputNumber as AntInputNumber } from 'antd'

import styles from './Input.module.less'

export const Input = ({
  type,
  error: errorMessage,
  className,
  prefix,
  suffix,
  margin = true,
  fontSize,
  underline,
  ...props
}) => {
  const generatedProps = {
    className: cn(styles.input, className, { [styles._underline]: underline }),
    ...props,
  }

  const control =
    type === 'number' ? (
      <AntInputNumber {...generatedProps} />
    ) : (
      <AntInput {...generatedProps} onChange={e => props.onChange(e.target.value)} />
    )

  return (
    <div className={styles.container}>
      {prefix}
      <span className={styles.inputContainer}>{control}</span>
      {suffix}
    </div>
  )
}

Input.propTypes = {
  value: PropTypes.any,
  error: PropTypes.string,
  type: PropTypes.string,
  className: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func,
  prefix: PropTypes.node,
  suffix: PropTypes.node,
  margin: PropTypes.bool,
  fontSize: PropTypes.string,
  underline: PropTypes.bool,
}
