import React from 'react'
import PropTypes from 'prop-types'

import { BaseField } from '../BaseField'
import { Label, LabelSizeType } from '../Label'
import { Input } from '../../../Input'

import styles from './TextField.module.less'

export const TextField = ({ style, id, required, name, label, error, suffix, labelSize, ...props }) => {
  return (
    <BaseField style={style} error={error} {...props}>
      {parsedProps => (
        <div className={styles.container}>
          <Label htmlFor={id || props.name} required={required} size={labelSize}>
            {label}
          </Label>
          <Input {...parsedProps} id={id || props.name} suffix={suffix} />
        </div>
      )}
    </BaseField>
  )
}

TextField.propTypes = {
  labelSize: LabelSizeType,
  id: PropTypes.string,
  onChange: PropTypes.func.isRequired,
  style: PropTypes.object,
  required: PropTypes.bool,
  name: PropTypes.string,
  label: PropTypes.node,
  error: PropTypes.node,
  suffix: PropTypes.node,
}
