import React from 'react'
import PropTypes from 'prop-types'

import { Switch } from '../../../Switch'
import { BaseField } from '../BaseField'
import { Label, LabelSizeType } from '../Label'

import styles from './SwitchField.module.less'

export const SwitchField = ({ style, label, required, labelSize, ...props }) => {
  return (
    <BaseField style={style} {...props} justify>
      {parsedProps => (
        <>
          <Label className={styles.label} required={required} size={labelSize} inline>
            {label}
          </Label>
          <Switch height="auto" {...parsedProps} checked={Boolean(props.value)} />
        </>
      )}
    </BaseField>
  )
}

SwitchField.propTypes = {
  labelSize: LabelSizeType,
  value: PropTypes.oneOfType([PropTypes.bool, PropTypes.string]),
  style: PropTypes.object,
  direction: PropTypes.string,
  required: PropTypes.bool,
  label: PropTypes.string,
  error: PropTypes.string,
}
