import React, { useEffect, useRef } from 'react'
import PropTypes from 'prop-types'
import cn from 'classnames'

import styles from './BaseField.module.less'

const triggerFirst = (() => {
  let timer = null
  return fn => {
    if (timer) {
      return
    }

    fn()
    timer = setTimeout(() => {
      clearTimeout(timer)
      timer = null
    }, 300)
  }
})()
export const BaseField = ({ style, children, error, disabled, globalFormReadonly, justify, ...restProps }) => {
  const ref = useRef()

  useEffect(() => {
    if (error) {
      triggerFirst(() => ref.current.scrollIntoView({ behavior: 'smooth', block: 'center' }))
    }
  }, [error])

  return (
    <div className={cn(styles.container, { [styles._justify]: justify })} style={style} ref={ref}>
      {children({
        ...restProps,
        disabled: disabled || globalFormReadonly,
      })}
      {error && <p className={styles.errorMessage}>{error}</p>}
    </div>
  )
}

BaseField.propTypes = {
  justify: PropTypes.bool,
  style: PropTypes.object,
  children: PropTypes.func,
  error: PropTypes.string,
  disabled: PropTypes.bool,
  globalFormReadonly: PropTypes.bool,
}
